<template>
	<div>
		<!-- <span id="date">Today</span> -->
	<el-row :gutter="12">
      <el-col :span="20" v-for="(item,index) in Array.prototype.reverse.call(detail)">
        <el-card shadow="hover" class="entryCard">
          <div class="colorBar"></div>
          <div class="emojiAudio">
            <img v-if="item.mood == 5" src="@/assets/emoji/emoji_verygood.png">
            <img v-if="item.mood == 4" src="@/assets/emoji/emoji_good.png">
            <img v-if="item.mood == 3" src="@/assets/emoji/emoji_neutral.png">
            <img v-if="item.mood == 2" src="@/assets/emoji/emoji_bad.png">
            <img v-if="item.mood == 1" src="@/assets/emoji/emoji_verybad.png">
            <audio v-if="item.src!= null " :src="item.src" controls></audio>
          </div>
          <div class="detailText">
            <div class="entryWords">
              <div class="day">{{item.timestamp | getDay}}</div>
              <div class='time'>{{item.timestamp | getTime}}</div>
              <div class="emotionClient">{{item.emotion_client}}</div>
              <div class="keywords"><span>Keywords</span> {{item.keyWords}}</div>
              <div class="distortions"><span>Distortions</span> {{item.cognitive}}</div>
            </div>
            <div class="text"> 
              {{item.text}}
              <img v-if="item.notes != null ":src="item.notes">
            </div>
          </div>
        </el-card>
      </el-col>
      <!-- <el-col :span="10">
        <el-card shadow="hover">
          Note 1
        </el-card>
        <el-card shadow="hover">
          Note 2
        </el-card>
      </el-col> -->
    </el-row>
	</div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        detail: '',
      }
    },
    mounted() {
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
              //then获取成功；response成功后的返回值（对象）
      .then(response=>{
         console.log(response.data);
         this.detail=response.data;
         this.detail.forEach(el => {
          if (el.src != null) {
            var src_index = el.src.split('/').length
            el.src = '../music/' + el.src.split('/')[src_index - 1]
          }
          if (el.notes != null) {
            var note_index = el.notes.split('/').length
            el.notes = '../img/' + el.notes.split('/')[note_index - 1]
          }
          
         })
      })
              //获取失败
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    filters: {
      getDay: function (value) {
        if (!value) return ''
        value = value.split('T')[0]
        return value
      },
      getTime: function (value) {
        if (!value) return ''
        value = value.split('T')[1].split('.')[0]
        return value
      },
      getSrc: function (value) {
        if (!value) return ''
        const index = value.split('/').length
        value = '../music' + value.split('/')[index - 1]
      }
    },
  }
</script>

<style>
  .entryCard {
    margin-top: 10px;

  }
  .entryCard .el-card__body {
    padding: 0 !important;
    vertical-align: top;
  }
  .colorBar {
    float: left;
    width: 3%;
    display: inline-block;
    background-color: #AADD01;
    height: 300px;
  }
  .emojiAudio {
    float: left;
    width: 30%;
    display: inline-block;
    margin-left: 6px;
    margin-top: 10px;
  }
  .emojiAudio img {
    height: 46px;
    display: block;
    margin-left: 20px;
    margin-bottom: 10px;
  }
  .entryWords div{
    margin-bottom: 6px;
  }
  .detailText {
    width: 50%;
    display: inline-block;
    margin-left: 30px;
  }
  .day,
  .time {
    color: #8F8F94;
  }
  .keywords span {
    font-weight: bold;
    font-size: 20px;
    color: #00577E;
    display: block;
  }
  .distortions span {
    font-weight: bold;
    font-size: 20px;
    color: #00577E;
    display: block;
  }
</style>


